.cover {
  background: url("../../../build/images/cover-inicio.webp") no-repeat top center;
  background-size: cover;
  height: 100%;
  width: 100%;
  padding: 300px;
  position: relative;
}
.cover::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.4);
}
.cover__title {
  z-index: 1;
  position: relative;
  text-align: center;
  color: #fff;
}

@media all and (min-width: 400px) {
  .cover {
    padding: 100px 0;
  }
}
/*HOME PAGE ABOUT*/
.cont-about {
  background-color: #46464f;
  padding: 2rem 1.8rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.cont-about__text p {
  margin-bottom: 1.6rem;
  color: #fe9c2d;
  font-family: var(--playfair);
  font-weight: bold;
  line-height: 1.6rem;
  font-size: 1.6rem;
}
.cont-about__text p ~ p {
  color: #46464f;
  font-family: var(--lato);
  font-weight: inherit;
  display: inline-block;
  line-height: 16px;
  font-size: 12px;
  margin-bottom: 2.5rem;
}
.cont-about__text .btn-primary {
  all: unset;
  background-color: #fe9c2d;
  color: #fff;
  border-radius: 5px;
  padding: 5px 8px;
  display: inline-block;
}
.cont-about__img {
  width: 100%;
  height: 100%;
}
.cont-about__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}